import * as React from 'react';
import { connect } from 'react-redux';
import { createSelector, BaseComponent, HTMLManager } from 'razy/dist/lib';
import * as _expressStatic from 'express-serve-static-core';
import REQUEST from '../const/request';
import { ROUTE_PATH } from '../routes';
import { WECHAT_AUTH } from '../const'

class GamePage extends BaseComponent<{}, {}> {
    async interceptor(req: _expressStatic.Request, res: _expressStatic.Response, next: _expressStatic.NextFunction): Promise<any> {
       
        // decide where to go
        try {
            const result = await _http.get(`${REQUEST.WHERE_SHOULD_I_GO}`)
            switch (result.routes) {
                case 'login':
                    res.redirect(`/${ROUTE_PATH.GAME_PREFIX}/${ROUTE_PATH.LOGIN}`)
                    break;
            
                default: throw new Error();
            }
        } catch (error) {
            // redirect to entry page
            
        }

        throw new Error('redirected');
    }
    setUpPage(manager: HTMLManager) {
       
    }
    getInitDataActionImp(props: any) { }
    constructor(props: any) {
        super(props);
        this.state = {};
    }
    componentDidMount() {
        super.componentDidMount();
    }
    render() {
        return (
            <div>
               {this.props.children}
             </div>
        );
    }
}

const selector = () => ({});

export = connect(selector)(GamePage);


